import "./Style.css";
import Header from "../components/Header";
import Footer from "../components/Footer";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function Hobby() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 20,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  const images = [
    {
      src: "img/book1.jpg",
      alt: " ",
    },
    {
      src: "img/book2.jpg",
      alt: "   ",
    },
    {
      src: "img/book3.jpg",
      alt: " ",
    },
    {
      src: "img/book4.jpg",
      alt: " ",
    },
  ];

  const images2 = [
    {
      src: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTgP-A8zY9i-wCEJCQpkb7Jrt9QlBk7zqG7aA&usqp=CAU",
      alt: "  ",
    },
    {
      src: "img/net1.jpg",
      alt: "  ",
    },
    {
      src: "img/net2.jpg",
      alt: ":   ",
    },
    {
      src: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQLQOoCUt7n-z0OO3HQNlXIAGz6NwBSSoY4UQ&usqp=CAU",
      alt: "   ",
    },
  ];

  const images3 = [
    {
      src: "img/game1.jpg",
      alt: " ",
    },
    {
      src: "img/game2.jpg",
      alt: " ",
    },
  ];
  return (
    <div style={{ textAlign: "center" }}>
      <h2></h2>
      <Slider {...settings} className="Slider">
        {images.map((image, index) => (
          <Slider key={index}>
            <img src={image.src} width={image.width} height={image.height} />
          </Slider>
        ))}
      </Slider>
      <h3></h3>
      <div id="section1">
        <Slider {...settings} className="Slider">
          {images2.map((images2, index) => (
            <Slider key={index}>
              <img
                src={images2.src}
                width={images2.width}
                height={images2.height}
              />
            </Slider>
          ))}
        </Slider>
      </div>
      <h3></h3>
      <div id="section2">
        <Slider {...settings} className="Slider">
          {images3.map((images3, index) => (
            <Slider key={index}>
              <img
                src={images3.src}
                width={images3.width}
                height={images3.height}
              />
            </Slider>
          ))}
        </Slider>
      </div>
      <h3></h3>
    </div>
  );
}
