//import React, { Component } from "react";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Autoplay } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/autoplay";

export default function Myslide() {
  const movePage = useNavigate();

  const images = [
    {
      src: "https://avatars.githubusercontent.com/u/50562603?s=400&u=736bea2b8f65dc8862d0ea8e753fb2efcc8df68d&v=4",
      width: "450",
      height: "590",
    },
    {
      src: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQUIBcfiQfs8XQiyxR5Rx0Le7TZml7TjVMa2A&usqp=CAU",
      width: "450",
      height: "590",
    },
    {
      src: "data:image/jpeg;base64,",
      width: "450",
      height: "590",
    },
    {
      src: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSPB7cfiXzqd1ZerAz2R58REJpMHL-FBxN7SMc9NsXpUTKSwV6hD-uqn2XpBkgc1tSo7j4&usqp=CAU",
      width: "450",
      height: "590",
    },
    {
      src: "https://github.com/YejinnKim/Joahaeng/blob/main/public/img/pic.jpg?raw=true",
      width: "450",
      height: "590",
    },
    {
      src: "data:image/jpeg;base64,",
      width: "450",
      height: "590",
    },
    {
      src: "http://newsroom.etomato.com/userfiles/KakaoTalk_20180419_143200726.jpg",
      width: "450",
      height: "590",
    },
  ];

  function goDetail() {
    movePage("/Detail");
  }

  return (
    <Swiper
      modules={[Navigation, Pagination, Autoplay]}
      navigation
      pagination={{ clickable: true }}
      spaceBetween={50}
      slidesPerView={4}
      autoplay={{ delay: 1800, disableOnInteraction: false }}
      onSlideChange={() => console.log("slide change")}
      onSwiper={(swiper) => console.log(swiper)}
      breakpoints={{
        328: {
          slidesPerView: 1,
        },
        640: {
          slidesPerView: 2,
        },
        768: {
          slidesPerView: 3,
        },
        1024: {
          slidesPerView: 4,
        },
      }}
    >
      {images.map((image, index) => (
        <SwiperSlide key={index}>
          <img
            onClick={goDetail}
            src={image.src}
            width={image.width}
            height={image.height}
            alt=""
          />
        </SwiperSlide>
      ))}
    </Swiper>
  );
}

